<template>
  <div class="info-box">
    <div v-if="contentData[contentValue]">
      <div v-for="(item, index) in contentData[contentValue].sections" :key="index">
        <h2 v-if="item.title">{{ item.title }}</h2>
        <p v-if="item.content" v-html="item.content"></p>
        <template v-if="item.items">
          <ul>
            <li v-for="(it, i) in item.items" :key="i">
              <p v-html="it.content"></p>
              <pre v-if="it.code"><code>{{ it.code }}</code></pre>
            </li>
          </ul>
        </template>
        <template v-if="item.questions">
          <p><strong>问题：</strong></p>
          <ul>
            <li v-for="(question, qIndex) in item.questions" :key="'q'+qIndex">
              {{ question }}
            </li>
          </ul>
          <p><strong>答案：</strong></p>
          <ul>
            <li v-for="(answer, aIndex) in item.answers" :key="'a'+aIndex" v-html="answer"></li>
          </ul>
        </template>
      </div>
    </div>
    <div v-else>
      <p>内容加载中...</p>
    </div>
  </div>
</template>

<script>
import contentData from "./assets/data";

export default {
  props: ["contentValue"],
  data() {
    return {
      contentData: contentData // 导入的数据
    };
  }
};
</script>

<style scoped>
.info-box {
  margin: 0 10px;
  border-radius: 8px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
 max-width: 907.9px;
}

.info-box h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 15px;
}

.info-box h2 {
  font-size: 20px;
  color: #555;
  margin-top: 20px;
  margin-bottom: 10px;
}

.info-box p {
  font-size: 16px;
  color: #666;
  margin-bottom: 10px;
}

.info-box code {
  padding: 2px 5px;
  border-radius: 4px;
  font-family: Consolas, monospace;
  color: red;
}

.info-box pre {
  background-color: #282c34;
  color: #abb2bf;
  padding: 15px;
  border-radius: 6px;
  overflow-x: auto;
  font-size: 14px;
  margin: 10px 0;
}

.info-box ul {
  padding-left: 20px;
  margin: 10px 0;
}

.info-box li {
  margin-bottom: 8px;
}

.info-box li pre {
  margin-top: 5px;
}
</style>