import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store(
    {
        state: {
            likes: 70,
            leaveMessages: [
                {name: '小明', content: 'hello world' },
                {name: '小红', content: '你好世界' },
                {name: '小刚', content: '哈哈哈哈' },
                {name:'小棋',content:'吼吼吼吼'},
                {name:'小蓝',content:'噢噢噢噢哦哦哦哦哦哦哦哦噢噢噢噢噢噢噢噢噢噢噢噢噢噢噢噢哦哦哦'},
                {name:'小蓝',content:'噢噢噢噢哦哦哦'},
                {name:'小蓝',content:'噢噢噢噢哦哦哦'},
            ]
        },
        mutations: {
            addLike(state) {
                state.likes++
            },
            addMessage(state, message) {
                state.leaveMessages.unshift(message)
            }
        }
    }
)
export default store