<template>
  <div class="container"> 
    <div class="toc-container">
      <ol @click="changeContent">
        <li v-for="(item, index) in tocItems" :key="index" 
            @click="toggleItem(index)"
            :class="{ 'has-children': item.children }">
          {{ item.title }}
          <ul v-if="item.children && item.expanded">
            <li v-for="(child, childIndex) in item.children" :key="childIndex">
              {{ child }}
            </li>
          </ul>
        </li>
      </ol>
    </div>
    <div class="content">
      <h3>{{tocItems[content].title}}</h3>
      <div class="content-item">
        <content-item :contentValue="content"></content-item>
      </div>
    </div>
  </div>
</template>

<script>
import contentItem from './blogContent.vue';

export default {
  components: {
    contentItem
  },
  data() {
    return {
      tocItems: [
        { title: "路由的基本使用" },
        { title: "声明式导航-导航链接" },
        { 
          title: "异步IO",
          children: ["Node异步I/O", "非I/O异步API", "区别"],
          expanded: false
        },
        {
          title: "异步编程",
          children: ["异步编程解决方案", "异步并发控制"],
          expanded: false
        },
        { title: "内存控制" },
        { title: "理解Buffer" },
        { title: "网络编程" },
        { title: "构建Web应用" },
        { title: "玩转进程" }
      ],
      content: 0 // 初始化为 0
    };
  },
  methods: {
    toggleItem(index) {
      if (this.tocItems[index].children) {
        this.$set(this.tocItems[index], 'expanded', !this.tocItems[index].expanded);
      }
    },
    changeContent(event) {
      const index = Array.from(event.target.parentNode.children).indexOf(event.target);
      this.content = index; // 更新 content 值为当前点击项的索引
    }
  }
};
</script>

<style scoped lang="less">
@keyframes move {
  0% {
    height: 0;
  }
  100% {
    height: 100%;
  }
  
}

.container {
  display: flex;
  flex-direction: row;
  gap: 20px;
  justify-content: center;
  padding: 50px;
  height: 100%; 
  box-sizing: border-box; 
  
  .toc-container {
    animation: move 0.5s ease-in-out;
    z-index: 1;
    border: 1px solid black;
    border-radius: 20px;
    height: 0;
    font-family: "PingFang SC", "Microsoft YaHei", sans-serif;
    line-height: 1.6;
    min-width: 150px;
    height: 100%;
    flex: 1;
    background-color: white;
    display: flex; 
    flex-direction: column; 
    overflow: hidden; 

    ol {
      list-style-type: decimal;
      overflow-y:auto;
      flex-grow: 1; 
      &:hover {
        cursor: pointer;
      }
      
      li {
        margin-bottom: 8px;
        
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    ul {
      list-style-type: disc;
      padding-left: 30px;
      margin: 5px 0;
    }

    .has-children {
      cursor: pointer;
      position: relative;
      padding-right: 20px;
    }
  }
  .content {
    animation: move 0.5s ease-in-out;
    z-index: 1;
    border-radius: 10px;
    flex: 3;
    border: 1px solid black;
    height: 100%;
    box-sizing: border-box;
    background-color: white;
    display: flex;
    flex-direction: column;
    h3{
    margin: 0;
    text-align: center;
    border-bottom: 1px solid #000;
    }
    .content-item {
    overflow: hidden;
    overflow-y: auto;
  }
}
}
</style>